<h4 class="fw-bold">Krew na śniegu – rozpoczyna się bitwa o Monte Cassino</h4>

    <p class="fw-bold">Bitwę o masyw Monte Cassino uważa się powszechnie za najkrwawsze starcie na froncie zachodnim II wojny światowej.
    Bilans walk nie byłby tak straszny, gdyby na ich początku alianccy dowódcy pamiętali o naukach Hannibala i Napoleona
    i nie popełniali tak karygodnych błędów, jak chociażby zbombardowanie klasztoru na Monte Cassino 15 lutego 1944
    roku.
    </p>
    <p>Po sukcesach wojsk alianckich w Afryce Północnej postanowiono iść za ciosem i rozpocząć walkę po drugiej stronie
    Morza Śródziemnomorskiego. W nocy z 9 na 10 lipca 1943 roku na plażach Sycylii wylądowali żołnierze amerykańscy,
    brytyjscy, kanadyjscy i australijscy. Niemcy i Włosi byli kompletnie zaskoczeni, ale ich obrona szybko się
    pozbierała i sprzymierzeni dopiero na początku września mogli przeskoczyć z Sycylii na południowy kraniec „włoskiego
    buta”. Desant pod Salerno 9 września 1943 roku osłaniały między innymi polskie niszczyciele i okręty podwodne.
    Szczególnie wsławił się wśród nich ORP „Dzik”, który w ciągu dwóch tygodni patrolowania Morza Liguryjskiego
    storpedował 14 nieprzyjacielskich okrętów.
    </p>
    <p>Lądowanie sprzymierzonych w południowych Włoszech było nie tylko sukcesem militarnym, lecz także przełomem
    politycznym – Włosi obalili faszystowski rząd Benito Mussoliniego i przeszli na stronę aliantów. Adolf Hitler nie
    zamierzał jednak darować Włochom tej zdrady, ani pozostawić w opałach swego „faszystowskiego guru” Mussoliniego.
    Kiedy 8 września Włosi ogłosili oficjalnie kapitulację, 40 niemieckich dywizji zajęło cały kraj, a większość
    sojuszniczej do tej pory armii włoskiej rozbrojono i zamknięto za drutami obozów jenieckich.
    </p>
<h4 class="fw-bold">Droga ślimaka po brzytwie</h4>
    <p>Człowiekiem, który miał udowodnić aliantom, że lądując we Włoszech wstąpili jednocześnie do piekła, został
    feldmarszałek Luftwaffe Albert Kesserling. Dowodził on najbitniejszymi żołnierzami w armii niemieckiej – strzelcami
    spadochronowymi, zwanymi przez nieprzyjaciół „zielonymi diabłami”. W tym czasie spadochroniarze niemieccy nie mieli
    już wielu okazji do desantów spadochronowych, lecz jako doborowa piechota okazali się świetni w obronie, o czym
    mieli się przekonać alianci, m.in. pod Monte Cassino. Kesserling, by powstrzymać marsz sprzymierzonych do Rzymu,
    stworzył na tym kierunku kilka pozycji obronnych: zimową, Bernhardta i Gustawa. Ta ostatnia opierała się na masywie
    Monte Cassino, który był postrachem dla wszystkich armii atakujących Italię od czasów starożytnych. To m.in. słynny
    wódz Kartagińczyków, Hannibal, wolał iść okrężną drogą przez Alpy i wytracić tam prawie wszystkie swoje słonie, niż
    narażać się na pewną klęskę u „wrót ryglujących Rzym”. Natomiast zdobywca Włoch, Napoleon Bonaparte, dał swoim
    ewentualnym naśladowcom prostą radę: „Włochy to but. Trzeba wchodzić w nie od góry”. W sztabie armii sprzymierzonych
    o tej przestrodze pamiętał jedynie rodak cesarza Francuzów, dowódca Francuskiego Korpusu Ekspedycyjnego, generał
    Alfons Juin. Znając przebieg włoskiej kampanii Napoleona, Juin był prawie pewien, że alianckie natarcie w kierunku
    Rzymu zostanie zatrzymane w rejonie masywu Monte Cassino. Dlatego proponował głównodowodzącemu sprzymierzonych, gen.
    Dwightowi D. Eisenhowerowi, by zamiast wikłać się w walki na południu Włoch, przeprowadzić desanty na północy – pod
    Livorno i Ankoną, czyli wejść we „włoski but” od góry. Niestety, tej mądrej rady nie chciał słuchać ani Eisenhower,
