    listopada 1943 roku po ciężkich walkach alianci przesunęli się na kolejną niemiecką linię obrony na rzece Rapido,
    czyli pozycję Bernhardta. Następnie dotarli do miasteczka Cassino i do podnóża masywnego wzgórza Monte Cassino, na
