    <h4 class="fw-bold">Blood in the snow - the battle for Monte Cassino begins</h4>

    <p class="fw-bold">The Battle of Monte Cassino is commonly considered the bloodiest battle on the Western Front of World War II. The balance of the battles would not be so terrible if at the beginning the Allied commanders remembered the teachings of Hannibal and Napoleon and did not commit such culpable mistakes as the bombing of the monastery at Monte Cassino on February 15, 1944.


    </p>
    <p>fighting on the other side of the Mediterranean Sea. On the night of July 9-10, 1943, American, British, Canadian and Australian soldiers landed on the beaches of Sicily. The Germans and Italians were completely taken aback, but their defense gathered quickly and the allies were not able to jump from Sicily to the southern end of the "Italian boot" until the beginning of September. The landing at Salerno on September 9, 1943 was covered by, among others, Polish destroyers and submarines. The ORP "Dzik", which torpedoed 14 enemy ships during two weeks of patrolling the Ligurian Sea, was especially famous among them.
    </p>
    <p>
The allied landing in southern Italy was not only a military success, but also a political breakthrough - the Italians overthrew the fascist government of Benito Mussolini and switched to the Allied side. However, Adolf Hitler did not intend to forgive Italy for this betrayal, or to leave his "fascist guru" Mussolini in distress. When the Italians officially announced their surrender on September 8, 40 German divisions took the entire country, and most of the allied Italian army was disarmed and closed behind barbed wire prisoner camps.

    </p>
<h4 class="fw-bold">The path of the snail along the razor

</h4>
    <p>Luftwaffe Field Marshal Albert Kesserling was the man who was to prove to the Allies that they had simultaneously entered Hell when they landed in Italy. He commanded the most outstanding soldiers in the German army - parachute shooters, called by the enemies "green devils". At that time, the German paratroopers did not have many opportunities for parachute landings, but as excellent infantry they proved to be excellent in defense, as the Allies were to find out about, e.g. at Monte Cassino. Kesserling, in order to stop the allied march to Rome, created several defensive positions in this direction: winter, Bernhardt and Gustav. The latter was based on the Monte Cassino massif, which was a terror to all armies attacking Italy from ancient times. This includes the famous chieftain of the Carthaginians, Hannibal, preferred to take a detour across the Alps and kill almost all of his elephants there, rather than risk some defeat at the "gate locking Rome". On the other hand, the conqueror of Italy, Napoleon Bonaparte, gave his potential followers a simple piece of advice: “Italy is a shoe. You have to enter them from the top ”. In the headquarters of the allied armies, this warning was remembered only by the countryman of the French Emperor, the commander of the French Expeditionary Corps, General Alfons Juin. Knowing the course of Napoleon's Italian campaign, Juin was pretty sure that the Allied attack towards Rome would be stopped in the Monte Cassino area. That is why he proposed to the commander-in-chief of the allies, General Dwight D. Eisenhower, to instead of getting involved in fighting in the south of Italy, to carry out landings in the north - at Livorno and Ancona, that is, to enter the "Italian shoe" from above. Unfortunately, neither Eisenhower nor the other Anglo-Saxon commanders wanted to heed this wise advice. This clearly proved that the Allied staff officers were not aware of the terrain obstacles that would await them there. Moreover, they associated Italy with tourist folders of the "sunny paradise" and they did not take into account that winter and spring would turn Italian roads into swamps, and swollen rivers into impassable places. In addition, the soldiers of Field Marshal Kesserling did an excellent job of sapper, preparing the field fortifications of the above-mentioned defense lines. It is worth mentioning that the Germans relied on the field fortifications of the Polish Army, which they had great difficulty breaking through in September 1939.


    </p>
    <p>All this meant that the Allied offensive clearly slowed down in October 1943. The following months on the Apennine Peninsula were for the allies a slow "sliding of the snail over the razor's edge". At the beginning of November 1943, after heavy fighting, the Allies shifted to another German line of defense on the Rapido River, i.e. the position of Bernhardt. Then they reached the town of Cassino and the foot of the massive Monte Cassino hill, where they saw the walls of the famous Benedictine abbey. Unfortunately, they did not have much time to admire one of the architectural masterpieces, as German cannons and machine guns fired at them from the hill. It turned out that parachute shooters were blocking the only road allowing for free movement of troops - the road running along the coast towards Rome. This is where the Allied successes of the second half of 1943 came to an end.
    </p>
<h4 class="fw-bold">Voracious mountains</h4>
    <p>In the described period, i.e. at the turn of 1943 and 1944, actions on subsequent lines of German defense in Italy took the form of position fights, so well known from the times of World War I or some campaigns on the Eastern Front in World War II. When the main forces of the fighting sides stayed opposite each other for a long time, firing artillery and machine-guns from time to time, the "patrol work" began. This was also the case on the Garigliano and Rapido rivers, and Polish commandos from the 1st Independent Commando Company, commanded by Maj. Władysław Smrokowski. From December 12, 1943, the company fought in the Italian mountains as part of the British 2nd Brigade for Special Tasks of General Tom Churchill. The soldiers of major Smrokowski distinguished themselves, among others, by the rally to Monte Valle Martina, which they conquered with the British from the Queens Regiment on January 17, 1944, but this success did not make spring in the Allied camp in the new 1944. At the end of January, Polish commandos with considerable losses in the killed and wounded were withdrawn from the front and shortly thereafter incorporated into the 2nd Polish Corps.
    </p>
    <p>The minor successes of the Allied commandos or scouts could not obscure the truth that the situation of the allied forces at the foot of Monte Cassino was bad. The first attempt to capture the hill was made on November 5, 1943 by the British 56th Infantry Division and the US 3rd Infantry Division. The attack was unsuccessful. At the same time, the British 8th Army of General Bernard L. Montgomery launched an offensive on the eastern side of the Apennine Peninsula, but here too the troops made little progress beyond the German winter position, towards the Bernhard line on the Sangro River. Meanwhile, at the end of November, another attempt to break the defense of the Monte Cassino massif was made by the American 5th Army of General Mark W. Clark. After the murderous fights in December, the Monte Camino hill was captured, and at the end of January 1944, Monte Samourco and Monte Luka. The Americans paid for all this with huge losses. Graphics from a German propaganda flyer, in which Italian mountains with mouths full of teeth like sharks devour entire Anglo-Saxon troops, has become a reality.


    </p>
    <p>Faced with the hecatomb of their own troops, the Allied commanders decided to try to unexpectedly enter the rear of the German defense lines. In Operation Single, on January 22, 1944, a heavy landing was planted in the Anzio-Nettuno area about 50 kilometers southeast of Rome. The Germans were completely taken by surprise, but instead of moving quickly forward, the American general John P. Lucas, commanding the landing force, began to strengthen the beach foothold. This gave Field Marshal Kesserling time to strengthen his defense at Anzio, and the Americans were stuck there for good - four months to be exact. Gen. Lucas was relieved of command and sent to the United States for his "merits". Unfortunately, after this opportunity was lost, it was necessary to plan another attack on the Monte Cassino massif, which the Germans, after shortening their lines of defense, called the position of Gustav.



    </p>
<h4 class="fw-bold">The biggest mistake of the Italian campaign</h4>
    <p>After the Americans bled out in the next attack on Monte Cassino, at the beginning of February 1944, this task, seemingly impossible, was assigned to the New Zealanders under the command of General Bernard Freyberg. After many deliberations at the headquarters, Freyberg said that he would not send his men to fight until the Benedictine monastery on Monte Cassino Hill was bombed. The New Zealander, having read in some popular science that for centuries it was an impregnable fortress, decided that the monastery is the biggest problem that stands in the way of the success of his troops. However, it was not only a fortress, but above all one of the cradles of Christianity, in whose walls great works of art and writings were gathered, and both warring parties declared that in their hostilities they would protect the European cultural heritage in Italy. For this reason, coming from the Catholic, Bavarian Kesserling family (otherwise he did not hesitate to bomb Warsaw in 1939), he refused to fortify the monastery. In the end, the Americans allowed the bombing, but had previously informed the monks and the Holy See about it. Thanks to the Germans, people and valuable collections were evacuated from the monastery.
    </p>
    <p>On the morning of February 15, 1944, 255 American "flying fortresses" dropped over 350 tons of demolition bombs on Monte Cassino, which blew the monastery's thick walls like a house of cards. How big a mistake this raid was, if not stupid, General Freyberg realized very quickly. His brave Maori and Gurkhas, despite their famous contempt for death, did not manage to throw German parachute shooters out of the bunkers on February 17, or during another bloody attack - on March 15, 1944. The paratroopers, having seized the bombed ruins of the monastery, turned it into a real impregnable fortress. The commander of the Allied forces in Italy, General Harold Alexander, said then: "Unfortunately, we are fighting the best soldiers in the world." On the other hand, the commander of the 5th Army, General Mark Clark, admitted bluntly after the war: “The bombing of the monastery was not only an unnecessary psychological error from the propaganda point of view, it was also a first class error from the military point of view. It only made the task more difficult and increased the loss of people and equipment, also a waste of time. " Historians are still discussing this phenomenon of recklessness, and some go as far as to conclude that it was a manifestation of the age-old rivalry of the Protestant camp (the English and the Americans) with the papists.
    </p>
    <p>Leaving aside these controversial theories, it should be stated that in May 1944 the "papists" set out to conquer the ruins of Monte Cassino, and Poles - in the allied forces. For when, at the turn of 1943 and 1944, commandos from the Polish 1st Independent Commando Company conducted their raids in the Italian mountains, in Taranto in southern Italy, the first units of the 2nd Polish Corps of General Władysław Anders went ashore from ships and ships. Soon his soldiers were to take part in the battle of the best soldiers in the world.


    </p>
<h4 class="fw-bold">Bibliography: </h4>
