    <div v-else-if="lang===`it`">
    <h4 class="fw-bold">Sangue nella neve - inizia la battaglia per Monte Cassino</h4>

    <p class="fw-bold">La battaglia di Monte Cassino è comunemente considerata la battaglia più sanguinosa sul fronte occidentale della seconda guerra mondiale. Il bilancio delle battaglie non sarebbe così terribile se all'inizio i comandanti alleati ricordassero gli insegnamenti di Annibale e Napoleone e non commettessero errori colposi come il bombardamento del monastero di Monte Cassino il 15 febbraio 1944.


    </p>
    <p>Dopo i successi delle forze alleate in Nord Africa, si è deciso di seguire il colpo e iniziare a combattere dall'altra parte del Mar Mediterraneo. Nella notte tra il 9 e il 10 luglio 1943, soldati americani, britannici, canadesi e australiani sbarcarono sulle spiagge della Sicilia. I tedeschi e gli italiani furono completamente colti di sorpresa, ma la loro difesa si raccolse rapidamente e gli alleati non furono in grado di saltare dalla Sicilia all'estremità meridionale dello "stivale italiano" fino all'inizio di settembre. Lo sbarco a Salerno il 9 settembre 1943 fu coperto, tra gli altri, da cacciatorpediniere e sottomarini polacchi. Tra loro era particolarmente famoso l'ORP "Dzik", che silurò 14 navi nemiche durante due settimane di pattugliamento nel Mar Ligure.


    </p>
    <p>
Lo sbarco alleato nell'Italia meridionale non fu solo un successo militare, ma anche una svolta politica: gli italiani rovesciarono il governo fascista di Benito Mussolini e passarono alla parte alleata. Tuttavia, Adolf Hitler non intendeva perdonare l'Italia per questo tradimento, né lasciare in difficoltà il suo "guru fascista" Mussolini. Quando gli italiani annunciarono ufficialmente la loro resa l'8 settembre, 40 divisioni tedesche presero l'intero paese e la maggior parte dell'esercito italiano alleato fu disarmato e rinchiuso dietro campi di prigionia di filo spinato.

    </p>
<h4 class="fw-bold">Il percorso della lumaca lungo il rasoio

</h4>
    <p>Il feldmaresciallo della Luftwaffe Albert Kesserling era l'uomo che doveva dimostrare agli alleati che erano entrati simultaneamente all'Inferno quando erano sbarcati in Italia. Comandava i soldati più importanti dell'esercito tedesco: i tiratori di paracadute, chiamati dai nemici "diavoli verdi". A quel tempo, i paracadutisti tedeschi non avevano molte opportunità per gli atterraggi con il paracadute, ma come fanteria eccellente si dimostrarono eccellenti in difesa, come gli Alleati dovevano scoprire, ad es. a Monte Cassino. Kesserling, per fermare la marcia degli alleati verso Roma, creò diverse postazioni difensive in questa direzione: inverno, Bernhardt e Gustav. Quest'ultimo era basato sul massiccio di Monte Cassino, che era un terrore per tutti gli eserciti che attaccavano l'Italia fin dai tempi antichi. Questo include il famoso capo dei Cartaginesi, Annibale, che preferì fare una deviazione attraverso le Alpi e uccidere quasi tutti i suoi elefanti lì, piuttosto che rischiare qualche sconfitta alla "porta che chiude Roma". D'altra parte, il conquistatore d'Italia, Napoleone Bonaparte, ha dato ai suoi potenziali seguaci un semplice consiglio: “L'Italia è una scarpa. Devi inserirli dall'alto ”. Nel quartier generale degli eserciti alleati, questo avvertimento fu ricordato solo dal connazionale dell'imperatore francese, il comandante del Corpo di spedizione francese, il generale Alfons Juin. Conoscendo il corso della campagna italiana di Napoleone, Juin era abbastanza sicuro che l'attacco alleato contro Roma sarebbe stato fermato nell'area di Monte Cassino. Per questo ha proposto al comandante in capo degli alleati, il generale Dwight D.Eisenhower, di effettuare sbarchi nel nord, a Livorno e ad Ancona, invece di farsi coinvolgere nei combattimenti nel sud Italia. , per entrare nella "scarpa italiana" dall'alto. Sfortunatamente, né Eisenhower né gli altri comandanti anglosassoni volevano dare ascolto a questo saggio consiglio. Ciò ha dimostrato chiaramente che gli ufficiali di stato maggiore alleati non erano a conoscenza degli ostacoli sul terreno che li avrebbero aspettati lì. Inoltre, associavano l'Italia ai dépliant turistici del "paradiso soleggiato" e non tenevano conto del fatto che l'inverno e la primavera avrebbero trasformato le strade italiane in paludi e i fiumi in piena in luoghi impraticabili. Inoltre, i soldati del feldmaresciallo Kesserling hanno svolto un ottimo lavoro di geniere, predisponendo le fortificazioni da campo delle suddette linee di difesa. Vale la pena ricordare che i tedeschi facevano affidamento sulle fortificazioni da campo dell'esercito polacco, che ebbero grandi difficoltà a sfondare nel settembre 1939.


    </p>
    <p>
Tutto ciò fece sì che l'offensiva alleata subisse un netto rallentamento nell'ottobre 1943. I mesi successivi sulla penisola appenninica furono per gli alleati un lento "scivolamento della chiocciola sul filo del rasoio". All'inizio di novembre 1943, dopo pesanti combattimenti, gli Alleati si spostarono su un'altra linea di difesa tedesca sul fiume Rapido, ovvero la posizione di Bernhardt. Quindi raggiunsero il paese di Cassino e ai piedi del massiccio colle di Monte Cassino, dove videro le mura della famosa abbazia benedettina. Sfortunatamente, non ebbero molto tempo per ammirare uno dei capolavori architettonici, poiché cannoni e mitragliatrici tedeschi spararono contro di loro dalla collina. Si è scoperto che i tiratori con il paracadute stavano bloccando l'unica strada che consentiva la libera circolazione delle truppe: la strada che corre lungo la costa verso Roma. È qui che finirono i successi alleati della seconda metà del 1943.

    </p>
<h4 class="fw-bold">Montagne voraci</h4>
    <p>
Nel periodo descritto, cioè a cavallo tra il 1943 e il 1944, le azioni sulle successive linee di difesa tedesca in Italia presero la forma di lotte di posizione, così ben note dai tempi della prima guerra mondiale o di alcune campagne sul fronte orientale nella guerra mondiale. II. Quando le forze principali delle parti combattenti rimasero a lungo l'una di fronte all'altra, sparando di tanto in tanto con artiglieria e mitragliatrici, iniziò il "lavoro di pattuglia". Questo è stato anche il caso dei fiumi Garigliano e Rapido, e dei commando polacchi della 1a Compagnia di Commando Indipendente, comandata dal Magg. Władysław Smrokowski. Dal 12 dicembre 1943, la compagnia combatté sulle montagne italiane come parte della 2a Brigata britannica per compiti speciali del generale Tom Churchill. I soldati del maggiore Smrokowski si distinsero, tra gli altri, per il raduno al Monte Valle Martina, che conquistarono con gli inglesi del Reggimento Regine il 17 gennaio 1944, ma questo successo non fece primavera nel campo alleato nel nuovo 1944 Alla fine di gennaio, i commando polacchi con notevoli perdite di morti e feriti furono ritirati dal fronte e poco dopo incorporati nel 2 ° Corpo d'Armata Polacco.

    </p>
    <p>I piccoli successi dei commando o esploratori alleati non potevano oscurare la verità che la situazione delle forze alleate ai piedi di Monte Cassino era brutta. Il primo tentativo di catturare la collina fu effettuato il 5 novembre 1943 dalla 56a divisione di fanteria britannica e dalla 3a divisione di fanteria degli Stati Uniti. L'attacco non ha avuto successo. Allo stesso tempo, l'8a armata britannica del generale Bernard L. Montgomery lanciò un'offensiva sul versante orientale della penisola appenninica, ma anche qui le truppe fecero pochi progressi oltre la posizione invernale tedesca, verso la linea Bernhard sul fiume Sangro. Intanto, a fine novembre, un altro tentativo di rompere la difesa del massiccio di Monte Cassino è stato compiuto dalla 5a Armata americana del generale Mark W. Clark. Dopo gli scontri omicidi di dicembre, la collina di Monte Camino fu conquistata e, alla fine di gennaio 1944, il Monte Samourco e il Monte Luka. Gli americani hanno pagato tutto questo con enormi perdite. La grafica di un volantino di propaganda tedesca, in cui montagne italiane con la bocca piena di denti come squali divorano intere truppe anglosassoni, è diventata una realtà.



    </p>
    <p>Di fronte all'ecatombe delle proprie truppe, i comandanti alleati decisero di tentare di entrare inaspettatamente nelle retrovie delle linee di difesa tedesche. Nell'Operazione Singola, il 22 gennaio 1944, fu piantato un pesante atterraggio nella zona di Anzio-Nettuno a circa 50 chilometri a sud-est di Roma. I tedeschi furono completamente colti di sorpresa, ma invece di avanzare rapidamente, il generale americano John P. Lucas, al comando della forza di sbarco, iniziò a rafforzare il punto d'appoggio sulla spiaggia. Ciò diede al feldmaresciallo Kesserling il tempo di rafforzare la sua difesa ad Anzio, e gli americani rimasero bloccati lì per sempre, quattro mesi per essere precisi. Il generale Lucas fu sollevato dal comando e inviato negli Stati Uniti per i suoi "meriti". Perduta questa occasione, purtroppo, fu necessario pianificare un altro attacco al massiccio del Monte Cassino, che i tedeschi, dopo aver accorciato le loro linee di difesa, chiamarono la posizione di Gustav.



    </p>
<h4 class="fw-bold">L'errore più grande della campagna italiana</h4>
    <p>Dopo che gli americani si dissanguarono nel successivo attacco a Monte Cassino, all'inizio di febbraio 1944, questo compito, apparentemente impossibile, fu assegnato ai neozelandesi al comando del generale Bernard Freyberg. Dopo molte deliberazioni al quartier generale, Freyberg ha detto che non avrebbe mandato i suoi uomini a combattere fino a quando il monastero benedettino di Monte Cassino non fosse stato bombardato. Il neozelandese, avendo letto in alcune scienze popolari che per secoli è stata una fortezza inespugnabile, ha deciso che il monastero è il problema più grande che ostacola il successo delle sue truppe. Tuttavia, non era solo una fortezza, ma soprattutto una delle culle della cristianità, nelle cui mura erano raccolte grandi opere d'arte e scritti, ed entrambe le parti in guerra dichiararono che nelle loro ostilità avrebbero protetto il patrimonio culturale europeo in Italia. Per questo, proveniente dalla famiglia cattolica bavarese Kesserling (altrimenti non esitò a bombardare Varsavia nel 1939), si rifiutò di fortificare il monastero. Alla fine, gli americani hanno permesso il bombardamento, ma ne avevano preventivamente informato i monaci e la Santa Sede. Grazie ai tedeschi, persone e preziose collezioni furono evacuate dal monastero.
    </p>
    <p>La mattina del 15 febbraio 1944, 255 "fortezze volanti" americane sganciarono oltre 350 tonnellate di bombe demolitrici su Monte Cassino, che fecero saltare le spesse mura del monastero come un castello di carte. Il generale Freyberg si rese conto molto rapidamente di quanto grande errore fosse questo raid, se non stupido. I suoi coraggiosi Maori e Gurkhas, nonostante il loro famoso disprezzo per la morte, non riuscirono a lanciare i tiratori con paracadute tedeschi fuori dai bunker il 17 febbraio, o durante un altro sanguinoso attacco - il 15 marzo 1944. I paracadutisti, dopo aver sequestrato le rovine bombardate di il monastero, lo trasformò in una vera e propria fortezza inespugnabile. Il comandante delle forze alleate in Italia, il generale Harold Alexander, ha detto allora: "Purtroppo stiamo combattendo i migliori soldati del mondo". D'altra parte, il comandante della 5a armata, il generale Mark Clark, ha ammesso senza mezzi termini dopo la guerra: "Il bombardamento del monastero non è stato solo un errore psicologico non necessario dal punto di vista della propaganda, è stato anche un errore di prima classe da il punto di vista militare. Ha solo reso il compito più difficile e aumentato la perdita di persone e attrezzature, anch'essa una perdita di tempo. "Gli storici stanno ancora discutendo di questo fenomeno di incoscienza, e alcuni arrivano a concludere che fosse una manifestazione della secolare rivalità del campo protestante (gli inglesi e gli americani) con i papisti.


    </p>
    <p>Lasciando da parte queste controverse teorie, va affermato che nel maggio 1944 i "papisti" si misero a conquistare le rovine di Monte Cassino e dei polacchi - nelle forze alleate. Perché quando, a cavallo tra il 1943 e il 1944, i commando della 1a Compagnia di Commando Indipendente polacca condussero le loro incursioni sulle montagne italiane, a Taranto, nell'Italia meridionale, le prime unità del 2 ° Corpo polacco del generale Władysław Anders sbarcarono dalle navi navi. Presto i suoi soldati avrebbero preso parte alla battaglia dei migliori soldati del mondo.




    </p>
